.pre {
    white-space: pre;
    font-family: Consolas,Monaco,Andale Mono,Ubuntu Mono,monospace;
    background: #f5f2f0;
    margin: 0 0.2em;
    padding: 0.2em 0.2em;
}

.spacer {
    height: 10em;
    width: 1px;
}

.mini-spacer {
    height: 5em;
    width: 1px;
}

#resume .section {
    padding: 1.5rem;
}

#resume li {
    position: relative;
}

#resume ul {
    position: relative;
}

.vid-indicator {
    position: absolute;
    top: calc(50% - 1.2rem);
    left: -6rem;
}

.video-content {
    height: 60vh;
    display: flex;
    margin: auto;
}

.resume-page {
    display: flex;
    flex-direction: row;
}

.resume-nav {
    width: 40rem;
    position: relative;
}

.nav-content {
    padding: 5rem 0;
    position: sticky;
    top: 0;
}
